<template>
  <div class="el-radio-dict" ref="container">
    <div class="title">基础用法</div>
    <el-radio-dict @dictChange="handdleChange" clearable v-model="value1" dictType="PERSON_TYPE"></el-radio-dict>
    
    <div class="title">过滤数据</div>
    <el-radio-dict @dictChange="handdleChange"  clearable v-model="value2" :dictType="{type: 'PERSON_TYPE', filters: '001,002'}"></el-radio-dict>
    <el-radio-dict @dictChange="handdleChange"  clearable v-model="value3" :filterDataFun="filterDataFun" dictType="PERSON_TYPE"></el-radio-dict>
    
    <div class="title">禁用数据</div>
    <el-radio-dict @dictChange="handdleChange" dictType="PERSON_TYPE" clearable v-model="value4" :disableObj="{disableValue: '001,002'}"></el-radio-dict>
    <el-radio-dict @dictChange="handdleChange" dictType="PERSON_TYPE" :disabledDataFun="disabledDataFun" clearable v-model="value5"></el-radio-dict>
   
    <div class="title">配置keyValue及赋值</div>
    <el-radio-dict @dictChange="handdleChange" clearable v-model="value6" dictType="PERSON_TYPE"></el-radio-dict>
    <el-radio-dict @dictChange="handdleChange" :keyValue="true" clearable v-model="value7" dictType="PERSON_TYPE"></el-radio-dict>
  </div>
</template>

<script>
export default {
  name: "el-radio-dict-index",
  data() {
    return {
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "001",
      value7: "001"
    }
  },
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        const container = this.$refs.container
        const data = {"demoContainerHeight": container.scrollHeight + 20}
        window.parent.postMessage(data, "*")
      }, 1000)
    })
  },
  methods: {
    handdleChange(val) {
      console.log(val)
    },
    //过滤数据
    filterDataFun(list) {
      return list.filter(item => {
        return item.value === "001" || item.value === "002"
      })
    },
    //禁用数据
    disabledDataFun(list) {
      return list.map(item => {
        return {
          ...item,
          disabled: !!(item.value === "001" || item.value === "002")
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-radio-dict{
  min-height: 300px;
  .el-radio-group{
    display: block;
    &+.el-radio-group {
      margin-top: 20px;
    }
  }
}
</style>